import React from "react";
import Carousel from "react-bootstrap/Carousel";
import Image1 from './Image1.jpg';
import Image2 from './Image2.jpeg';
import Image3 from './Image3.jpg';
import LandingImage from '../SharedComponents/LandingImage';
const CarosualHome = () => {
  return (
    <React.Fragment>
      <Carousel>
        <Carousel.Item>
            <LandingImage content={{
                image:Image1,
                title:"Phoenix",
                discritpion:"Transform yourself into a Ninja in technology"
                }}/>
          </Carousel.Item>
          <Carousel.Item>
            <LandingImage content={{
                image:Image2,
                title:"Electronics",
                discritpion:"Learning all about Electriciy & The digital World"
                }}/>
          </Carousel.Item>
          <Carousel.Item>
            <LandingImage content={{
                image:Image3,
                title:"Robotics",
                discritpion:"Design your own Robot and make it do whatever you want"
                }}/>
          </Carousel.Item>
      </Carousel>
    </React.Fragment>
  );
};

export default CarosualHome;
